import React, { useState, useCallback, useEffect } from 'react';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Table from 'react-bootstrap/Table';
import manageResponse from './manageResponse';
import SiteSelector from './siteselector';
import { useLocalStorage } from './useLocalStorage';
import { toast } from 'react-toastify';
import * as dayjs from 'dayjs';
var localizedFormat = require('dayjs/plugin/localizedFormat')
dayjs.extend(localizedFormat)

const useSortableData = (items, config = null) => {
  const [sortConfig, setSortConfig] = useState(config);

  const sortedItems = React.useMemo(() => {
    let sortableItems = [...items];
    if (sortConfig !== null) {
      sortableItems.sort((a, b) => {
        if (a[sortConfig.key] < b[sortConfig.key]) {
          return sortConfig.direction === 'ascending' ? -1 : 1;
        }
        if (a[sortConfig.key] > b[sortConfig.key]) {
          return sortConfig.direction === 'ascending' ? 1 : -1;
        }
        return 0;
      });
    }
    return sortableItems;
  }, [items, sortConfig]);

  const requestSort = key => {
    let direction = 'ascending';
    if (sortConfig && sortConfig.key === key && sortConfig.direction === 'ascending') {
      direction = 'descending';
    }
    setSortConfig({ key, direction });
  }

  return { items: sortedItems, requestSort };
}

function SearchResultList({ data, addstudycallback }) {
  const { items, requestSort } = useSortableData(data);

  return (
      <div>
        <Table striped bordered>
          <thead>
            <tr>
              <th><button type="button" onClick={() => requestSort('PatientName')}>Name</button></th>
              <th><button type="button" onClick={() => requestSort('PatientID')}>PatientID</button></th>
              <th><button type="button" onClick={() => requestSort('StudyDate')}>StudyDate</button></th>
              <th>Mod</th>
              <th>Description</th>
              <th>Sex</th>
              <th>Birthdate</th>
              <th>InstitutionName</th>
            </tr>
          </thead>
          <tbody>
          { items.map(item => { return (
            <tr key={item.patientStudyId}>
              <td>{item.PatientName}</td>
              <td>{item.PatientID}</td>
              <td>{item.StudyDateFormatted}</td>
              <td>{item.ModalitiesInStudy}</td>
              <td>{item.StudyDescription}</td>
              <td>{item.PatientSex}</td>
              <td>{item.PatientBirthDateFormatted}</td>
              <td>{item.InstitutionName}</td>
              <td><Button variant="primary" onClick={(event) => {addstudycallback(item)}}>Add</Button></td>
            </tr>
          )})}
          </tbody>
        </Table>
      </div>

  )
}

export default function SearchPage() {
  const [searchresultlist, setsearchresultlist] = useState([]);
  const [selectedsiteid, setselectedsiteid] = useState("");
  const [token] = useLocalStorage('token', "");
  const [siteRegion, setsiteRegion] = useState("");
  const [patientname, setpatientname] = useState("");
  const [patientid, setpatientid] = useState("");
  const [studydate, setstudydate] = useState("");
  const [studyinstanceuid, setstudyinstanceuid] = useState("");
  const [burnstudylist, setburnstudylist] = useState([]);
  const [burninglink, setburninglink] = useState("");

  const search = useCallback(() => {
    var querystring = "";
    if(patientname)
      querystring += 'PatientName=' + patientname;

    if(patientid) {
      if(querystring.length > 0)  querystring += '&';
      querystring += 'PatientID=' + patientid;
    }

    if(studydate) {
      if(querystring.length > 0)  querystring += '&';
      querystring += 'StudyDate=' + studydate;
    }
    if(studyinstanceuid) {
      // overide any previous query
      querystring = 'StudyInstanceUID=' + studyinstanceuid;
    }

    var regionurl = null;
    switch(siteRegion) {
      case 'us':
        regionurl = process.env.REACT_APP_ESPRESSOAPI_URL + '/api/studies/search?siteId=' + selectedsiteid + '&' + querystring;
        break;
      case 'tw':
        regionurl = process.env.REACT_APP_ESPRESSOAPI_URL_TW + '/api/studies/search?siteId=' + selectedsiteid + '&' + querystring;
        break;
      default:
        regionurl = process.env.REACT_APP_ESPRESSOAPI_URL + '/api/studies/search?siteId=' + selectedsiteid + '&' + querystring;
        break;
    }

    fetch(regionurl, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((result1json) => {
      var studies = result1json.studies || [];
      for (var index = 0;index < studies.length; index++) {
        var item = studies[index];
        item.StudyDateFormatted = dayjs(item.StudyDate).format('l');
        item.PatientBirthDateFormatted = dayjs(item.PatientBirthDate).format('l');
      }

      setsearchresultlist(studies);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [setsearchresultlist, patientname, patientid, studydate, studyinstanceuid, selectedsiteid, siteRegion, token]);

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/sites/' + selectedsiteid, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.site || {};
      setsiteRegion(result.siteRegion);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [selectedsiteid, token]);

  const addstudycallback = useCallback((study) => {
    if(!burnstudylist.find(e => (e.patientStudyId === study.patientStudyId))) {
      var list = [ ...burnstudylist, study];
      setburnstudylist(list);
    }
  }, [burnstudylist, setburnstudylist])

  const removestudycallback = useCallback((study) => {
    var pos = burnstudylist.findIndex(e => (e.patientStudyId === study.patientStudyId));
    if(pos !== -1) {
      console.log(study);
      var list = [...burnstudylist.slice(0, pos), ...burnstudylist.slice(pos + 1)];
      setburnstudylist(list);
    }
  }, [burnstudylist, setburnstudylist])

  useEffect(() => {
    var result = "diorite://burn/?";
    result += "token=" + token;
    result += "&patientStudyId="
    for(var i = 0; i < burnstudylist.length; i++) {
      result += burnstudylist[i].patientStudyId;
      if(i < burnstudylist.length - 1)
        result += ",";
    }

    setburninglink(result);
  }, [burnstudylist, token]);

  return (
    <React.Fragment>
    <div className="row">
      <div className="col-md-4">
        <h3>Search</h3>
        <Form>
          <Form.Group as={Row} className="mb-3" >
            <Form.Label column sm="2" className="text-end">Site</Form.Label>
            <Col sm={9}><SiteSelector selectedsiteid={selectedsiteid} setselectedsiteid={(value) => {setselectedsiteid(value)}} /></Col>
          </Form.Group>
          <Form.Group as={Row} className="mb-3" >
            <Form.Label column sm="2" className="text-end">Name</Form.Label>
            <Col sm={9}><Form.Control type="text" name="patientname" value={patientname} onChange={(event) => {setpatientname(event.target.value)}} /></Col>
          </Form.Group>
          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm="2" className="text-end">ID</Form.Label>
            <Col sm={9}><Form.Control type="text" name="patientid" value={patientid} onChange={(event) => {setpatientid(event.target.value)}}/></Col>
          </Form.Group>
          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm="2" className="text-end">Date</Form.Label>
            <Col sm={9}><Form.Control type="date" name="studydate" value={studydate} onChange={(event) => {setstudydate(event.target.value)}}/></Col>
          </Form.Group>
          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm="2" className="text-end">StudyUID</Form.Label>
            <Col sm={9}><Form.Control type="text" name="studyinstanceuid" value={studyinstanceuid} onChange={(event) => {setstudyinstanceuid(event.target.value)}} /></Col>
          </Form.Group>
          <Form.Group as={Row} className="mb-3">
            <Col sm="2" />
            <Col sm={9}>
              <Button variant="primary" onClick={(event) => {event.preventDefault(); search(); }}>Search</Button>{' '}
              <Button variant="outline-secondary" onClick={(event) => {event.preventDefault();setpatientname("");setpatientid(""); setstudydate(""); setstudyinstanceuid("");setsearchresultlist([])}}>Clear</Button>
            </Col>
          </Form.Group>
        </Form>
      </div>
      <div className="col-md-8">
        <h3>Studies to burn <Button disabled={burnstudylist.length === 0} href={burninglink}>Burn (launch desktop app)</Button></h3>
        <p><a href="https://hicap.frontmotion.com/espresso/download/diorite.msi">Download Desktop App</a></p>
        <Table bordered>
          <thead>
            <tr><td>Name</td><td>ID</td><td>Date</td><td>Mod</td><td>Description</td><td>remove</td></tr>
          </thead>
          <tbody>
          { burnstudylist.map(item => { return (
            <tr key={item.patientStudyId}>
              <td>{item.PatientName}</td>
              <td>{item.PatientID}</td>
              <td>{item.StudyDateFormatted}</td>
              <td>{item.ModalitiesInStudy}</td>
              <td>{item.StudyDescription}</td>
              <td><Button variant="secondary" onClick={(event) => {removestudycallback(item)}}>Remove</Button></td>
            </tr>
          )})}
          </tbody>
        </Table>
      </div>
    </div>
    <div className="row">
      <div className="col-md-12">
        <SearchResultList data={searchresultlist} addstudycallback={addstudycallback}/>
      </div>
    </div>
    </React.Fragment>
  );
}
