import React, { useEffect, useState } from 'react';
import Form from 'react-bootstrap/Form';
import manageResponse from './manageResponse';
import { toast } from 'react-toastify';
import { useLocalStorage } from './useLocalStorage';

export default function SiteSelector({selectedsiteid, setselectedsiteid}) {
  const [token] = useLocalStorage('token', "");
  const [resultlist, setresultlist] = useState([]);

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/sites', { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.sites || [];

      setresultlist(result);
      if(!selectedsiteid)
        setselectedsiteid(result[0].siteId);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [selectedsiteid, token, setselectedsiteid]);

  return (
    <Form.Select value={selectedsiteid} onChange={(event) => {setselectedsiteid(event.target.value)}}>
    {
      resultlist.map(site => {
        return (
          <option key={site.siteId} value={site.siteId}>{site.name + ' (' + site.siteId + ')'}</option>
          );
      })
    }
    </Form.Select>
  );
}
